.modal {
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
   z-index: -1;
  opacity: 0;
  transition: all .1s;
  &.active{
    opacity: 1;
    z-index: 1;
  }
}
